export default "import { useState } from \"react\"\n\nimport UICard from \"src/components/UI/UICard\"\nimport { UITab, UITabPanel, UITabPanels, UITabs } from \"src/components/UI/UITabs\"\n\nexport default function WithPanel() {\n    const [tab, setTab] = useState(1)\n\n    return (<>\n        <UICard>\n            <UITabs\n                tab={tab}\n                iconAlign=\"left\"\n                className=\"shadow-2\"\n                onChange={(tabIndex)=>{setTab(tabIndex)}}\n            >\n                <UITab caption=\"photo\" icon=\"camera\" />\n                <UITab caption=\"help\" icon=\"help\" />\n                <UITab caption=\"history\" icon=\"history\" />\n            </UITabs>\n            <UITabPanels tab={tab}>\n                <UITabPanel>\n                    <div className=\"block-title\">Photo</div>\n                    <div className=\"block\">\n                        Lorem ipsum dolor sit amet consectetur adipisicing elit.\n                    </div>\n                </UITabPanel>\n                <UITabPanel>\n                    <div className=\"block-title\">Help</div>\n                    <div className=\"block\">\n                        Lorem ipsum dolor sit amet consectetur adipisicing elit.\n                    </div>\n                </UITabPanel>\n                <UITabPanel>\n                    <div className=\"block-title\">History</div>\n                    <div className=\"block\">\n                        Lorem ipsum dolor sit amet consectetur adipisicing elit.\n                    </div>\n                </UITabPanel>\n            </UITabPanels>\n        </UICard>\n\n        <hr />\n\n        <UICard>\n            <UITabPanels tab={tab}>\n                <UITabPanel>\n                    <div className=\"block-title\">Photo</div>\n                    <div className=\"block\">\n                        Lorem ipsum dolor sit amet consectetur adipisicing elit.\n                    </div>\n                </UITabPanel>\n                <UITabPanel>\n                    <div className=\"block-title\">Help</div>\n                    <div className=\"block\">\n                        Lorem ipsum dolor sit amet consectetur adipisicing elit.\n                    </div>\n                </UITabPanel>\n                <UITabPanel>\n                    <div className=\"block-title\">History</div>\n                    <div className=\"block\">\n                        Lorem ipsum dolor sit amet consectetur adipisicing elit.\n                    </div>\n                </UITabPanel>\n            </UITabPanels>\n            <UITabs\n                tab={tab}\n                iconAlign=\"left\"\n                className=\"bg-transparent\"\n                onChange={(tabIndex)=>{setTab(tabIndex)}}\n            >\n                <UITab caption=\"photo\" icon=\"camera\" />\n                <UITab caption=\"help\" icon=\"help\" />\n                <UITab caption=\"history\" icon=\"history\" />\n            </UITabs>\n        </UICard>\n    </>)\n}\n";