export default "import { useState } from \"react\"\nimport { UITab, UITabs } from \"src/components/UI/UITabs\"\n\nexport default function Basic() {\n    const [tab, setTab] = useState(1)\n\n    return (\n        <div style={{width:'100%'}}>\n            <UITabs\n                tab={tab}\n                className=\"bg-transparent\"\n                onChange={(value)=>{setTab(value)}}\n            >\n                <UITab caption=\"Photo\" icon=\"camera\" />\n                <UITab caption=\"Help\" icon=\"help\"/>\n                <UITab caption=\"History\" icon=\"history\"/>\n            </UITabs>\n\n            <hr />\n\n            <UITabs\n                tab={tab}\n                onChange={(value)=>{setTab(value)}}\n            >\n                <UITab icon=\"camera\" />\n                <UITab icon=\"help\"/>\n                <UITab icon=\"history\"/>\n            </UITabs>\n\n            <hr />\n\n            <UITabs\n                tab={tab}\n                align=\"justify\"\n                iconAlign=\"left\"\n                onChange={(value)=>{setTab(value)}}\n            >\n                <UITab caption=\"photo\" icon=\"camera\" />\n                <UITab caption=\"help\" icon=\"help\"/>\n                <UITab caption=\"history\" icon=\"history\"/>\n            </UITabs>\n\n            <hr />\n\n            <UITabs\n                tab={tab}\n                onChange={(value)=>{setTab(value)}}\n            >\n                <UITab caption=\"photo\" />\n                <UITab caption=\"help\" />\n                <UITab caption=\"history\" />\n            </UITabs>\n        </div>\n    )\n}\n";